.experience{
    display: grid;
    grid-gap: 3%;
    min-width: 600px;
}
.experience p{
    letter-spacing: 1px;
}

.company{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: right;
        align-content: right;
    font-size: 1.25rem;
    letter-spacing: -2px;
    font-weight: 900;
    font-family: arial;
    -ms-flex-align: end;
        align-items: flex-end;
}

.tecnologyList{
    display: grid;
    -webkit-padding-start: 0;
    grid-template-columns:  repeat(5, 1fr);
    grid-gap: 0.25rem;
    max-width: 100%;
}

.tecnology{
    -webkit-box-shadow: 0 1px 5px grey;
            box-shadow: 0 1px 5px grey;
    margin: 7px;
    text-align: center;
}


.separator{
    background: black;
    -webkit-box-shadow: var(--pri-sh), var(--sec-sh), var(--ter-sh);
            box-shadow: var(--pri-sh), var(--sec-sh), var(--ter-sh);
    padding: 32px;
    color: var(--main-pr);
    border-radius: 50px;
}


