.hidden{
    display: none;
}

.experienceList{
    display: block grid;
    grid-gap: 3%;
    grid-template-columns: minmax(600px , 1fr));
    grid-auto-flow: column;
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
    overflow: hidden;
    padding: 0 0 3% 0;
}

.alternate{
    background: black;
    -webkit-box-shadow: 0 8px 10px 1px rgba(0,0,0,0.24), 0 3px 14px 2px rgba(0,0,0,0.22), 0 5px 5px -3px rgba(0,0,0,0.30);
            box-shadow: 0 8px 10px 1px rgba(0,0,0,0.24), 0 3px 14px 2px rgba(0,0,0,0.22), 0 5px 5px -3px rgba(0,0,0,0.30);
    padding: 32px;
    color: var(--main-pr);
}

.contactInfo{
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 100%;
    -ms-flex-pack: justify;
        justify-content: space-between;
}



